module.exports = {
    name: 'list',
    data() {
        return {
            user_list: [], //
            currentPage: 1,
            page_size:10,
            page_sizes:[10, 100, 200, 300],
            loading2:0,
                 //
            paginations: {
                current_page: 1,
                total: 0,
                page_size: 12,
                page_sizes: [3, 9, 12, 24],
                layout: "total, sizes, prev, pager, next, jumper"
            },
            batch_id: '', //id
            batch_flag: true, //true,false

            //
            search_data: {
                user_name: '',
                email: '',
            },

            //
            dialog: {
                show: false,
                user_info: {}
            },

            //
            sex_filters: {
                list: [{
                    text: '',
                    value: 1
                }, {
                    text: '',
                    value: 2
                }, {
                    text: '',
                    value: 3
                }],
                multiple: false
            },

            //
            status_filters: {
                list: [{
                    text: '',
                    value: 1
                }, {
                    text: '',
                    value: 2
                }],
                multiple: false
            },
     
        }
    },
    methods: {
        /**
         * 
         * @param  {object} item 
         * @return {string}      
         */
        formatterSex(item) {
            return item.sex == 1 ? '' : (item.sex == 2 ? '' : '');
        },


        /**
         * 
         * @param  {object} item 
         * @return {string}      
         */
        formatterStatus(item) {
            return item.status == 1 ? '' : '';
        },


        /**
         * 
         * @param  {number} value 
         * @param  {object} item  
         * @return {boolean}       true,false
         */
        filterSex(value, item) {
            return item.sex == value;
        },


        /**
         * 
         * @param  {number} value 
         * @param  {object} item  
         * @return {boolean}       true,false
         */
        filterStatus(value, item) {
            return item.status == value;
        },


        /**
         * 
         */
        onSearch() {
            // console.log(this.search_data);

            var sd = {};

            var query = this.$route.query;
            for (var p in query) {
                sd[p] = query[p];
            }

            for (var s in this.search_data) {
                sd[s] = this.search_data[s];

                if (!sd[s]) {
                    delete sd[s];
                }
            }


            this.$router.push({
                path: this.$route.path,
                query: sd
            });
        },


        /**
         * CheckBox
         * @param  {array} val 
         */
        onSelectionChange(val) {
            // console.log(val);
            if (val.length) {
                this.batch_flag = false;
                var ids = [];
                for (var i = 0; i < val.length; i++) {
                    ids.push(val[i].id);
                }
                this.batch_id = ids.join(',');
            } else {
                this.batch_flag = true;
                this.batch_id = '';
            }
        },


        /**
         * 
         * @param  {number} page 
         */
        onChangeCurrentPage(page) {
            this.paginations.current_page = page;
            this.getList({
                page,
                
            });
        },


        /**
         * 
         * @param  {number} size 
         */
        onChangePageSize(page_size) {
            this.paginations.page_size = page_size;
            this.getList({
                page_size,
               
            });
        },


        onAccessUser(user, index, list) {
            this.$$accessUser({
                id: user.id
            }, (data) => {
                user.status = user.status == 1 ? 2 : 1;
            });
        },


        /**
         * 
         */
        getList() {
            var data = {
                page: this.paginations.current_page,
                page_size: this.paginations.page_size
            };
            var query = this.$route.query;
    
            for (var k in query) {
                if (this.search_data[k] !== undefined) {
                    this.search_data[k] = query[k];
                    data[k] = query[k];
                }
            }

            this.$$api_user_selectUser(data, (data) => {
                this.user_list = data.data;
                this.paginations.total = data.meta.pagination.total;
            });


        }
    },

    mounted() {
        this.getList();

        //test dialog

        /* setTimeout(() => {
             this.onSelectUser(this.user_list[0]);
         }, 600);*/
    },
    watch: {
        '$route' (to, from) {
            this.getList();
        }
    }
}


