<template>
    <div class="form">
        <el-form style="margin:20px;width:60%;min-width:600px;" 
            label-width="100px" 
            :model="user_data"
            :rules="user_rules"
            ref='user_data'>


    <el-form-item class='edit-form' 
                label="" 
                prop='discount'>
<span>:{{limit_money}}</span>  <br>   <span>:{{dicsount.max_money}}</span>
<br><span>:{{dicsount.min_money}}</span>
            </el-form-item>

            <el-form-item class='edit-form' 
                label="" 
                prop='money'>
                <el-input
                 style="width:50%;"
                        v-model.number="user_data.money" placeholder='0.00'></el-input>
            </el-form-item>


            <el-form-item class='edit-form' 
                label="" 
                prop='bank_card'>
                <el-input
                 :disabled=true
                        v-model="user_data.bank_card" placeholder=''></el-input>
            </el-form-item>

                <el-form-item style="width:100%;" 
                      prop='bank'    
                      label="" >
                        <el-select 
                         :disabled=true
                        v-model="user_data.bank"
                        @change='selectbank'
                         placeholder="">
                          <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                          </el-option>
                        </el-select>
                </el-form-item>


            <el-form-item class="edit-form" 
                label=''
                 :disabled=true
                prop='bank_address'>
                <el-input type='textarea' 
                   :disabled=true
                    v-model="user_data.bank_address"
                    placeholder=''></el-input>
            </el-form-item>

                 <el-form-item class='edit-form' 
                label="" 
                prop='card_name'>
                <el-input
                        :disabled='true'
                        v-model="user_data.card_name" placeholder=''></el-input>
               </el-form-item>

                       <el-form-item class='edit-form' 
                label="" 
                prop='phonecode'>
                 <el-input  v-model="user_data.phonecode" placeholder="" style="width:50%;"></el-input>
                            <el-button :plain="true" v-on:click="plan" :disabled="paytime.disa">{{paytime.time}}</el-button>

               </el-form-item>


            <el-form-item>
                <el-button type="primary"  @click='save_user("user_data")'></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
    import CashJs from './Cash.js';
    module.exports=CashJs;
</script>

<style scoped>
 .edit-form{
        width:500px;
    }



  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }
</style>



